import $ from "properjs-hobo";



export default ( instance ) => {
    const item = instance.blockJson.product;
    const image = item.items[ 1 ] || item.items[ 0 ];
    const htmlText = $( instance.productJson.item.body ).find( ".sqs-block-html" )[ 0 ].innerText;

    // console.log( instance );

    return `
        <div class="stack stack--sub">
            <div class="stack__wrap">
                <div class="issue__shop">
                    <h4 class="issue__title">${item.excerpt || item.title}</h4>
                    <a class="issue__button _button _button--lit js-button_" data-block-json="" href="${item.fullUrl}">${item.structuredContent.customAddButtonText}</a>
                </div>
                <div class="issue__prod">
                    <div class="issue__image media js-media">
                        <div class="media__wrap">
                            <img class="media__node image js-lazy-image" data-img-src="${image.assetUrl}" data-variants="${image.systemDataVariants}" data-original-size="${image.originalSize}" />
                        </div>
                    </div>
                    <div class="issue__desc p">${htmlText}</div>
                </div>
            </div>
        </div>
    `;
};
